import React, {Component} from 'react';

export default class AddToDo extends Component {
	
	state = {
		id: "",
		title: "",
		status: "Pending"
	}

	handleIdChange = (event) => {
		this.setState({
			id: event.target.value
		});
	};

	handleTitleChange = (event) => {
		this.setState({
			title: event.target.value
		});
	};

	handleStatusChange = (event) => {
		this.setState({
			status: event.target.value
		});
	};

	handleSubmit = (event) => {
		event.preventDefault();
		
		this.props.onAdd({
			id: this.state.id,
			title: this.state.title,
			status: this.state.status
		});

		this.setState({
			id: "",
			title: "",
			status: "Pending"
		});

	}

	render = () => {
		return (
				<div>
					<h3>Add To-Do</h3>
					<form onSubmit={this.handleSubmit}>
						<div className="form-group">
							<input value={this.state.id} className="form-control" placeholder="Enter ID" onChange={this.handleIdChange} />
						</div>
						<div className="form-group">
							<input value={this.state.title} className="form-control" placeholder="Enter Title" onChange={this.handleTitleChange} />
						</div>
						<div className="form-group">
							<select value={this.state.status} className="form-control" placeholder="Enter Status" onChange={this.handleStatusChange} >
								<option value="Done">Done</option>
								<option value="Pending">Pending</option>
							</select>
						</div>
						<button type="submit" className="form-control btn btn-primary">Add To-Do</button>
					</form>
				</div>
			);
	}

}