import React, {Component} from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

import AddToDo from './AddToDo';

export default class ToDoList extends Component {

	state = {
		todos: [
			{id: '1', title: 'Push your code to github.', status: 'Done'},
			{id: '2', title: 'Email to your manager.', status: 'Pending'}
		]
	}

	deleteToDo = (todo) => {
		const filteredItems = this.state.todos.filter(x => x.id !== todo.id)

		this.setState({
			todos: filteredItems
		});
	};

	editToDo = (todo) => {
		this.setState(state => ({
			todos: state.todos.map(x => {
				if(x.id === todo.id) {
					return {
						...x,
						status: x.status === "Done" ? "Pending" : "Done"
					};
				} else {
					return x;
				}
			})
		}))
	}

	addToDo = (todo) => {
		this.setState({
			todos: [...this.state.todos, todo]
		});
	}

	render = () => {
		return (
				<div>
					<AddToDo onAdd={this.addToDo}></AddToDo>
					<h1>ToDoList</h1>
					<table className="table">
						<thead>
							<tr>
								<th>id</th>
								<th>Title</th>
								<th>Status</th>
								<th>Action</th>
							</tr>
						</thead>
						<tbody>

							{this.state.todos.map(x => {
								return (
										<tr key = {x.id}>
											<td>{x.id}</td>
											<td>{x.title}</td>
											<td style={{color: x.status === "Done" ? "green" : "red"}}>{x.status}</td>
											<td>
												<button className = "btn btn-primary" onClick={() => this.deleteToDo(x)}>
													<span>
														<FontAwesomeIcon icon="trash"></FontAwesomeIcon>
													</span>
												</button>
												<button className = "btn btn-primary" onClick={() => this.editToDo(x)}>
													<span>
														<FontAwesomeIcon icon="edit"></FontAwesomeIcon>
													</span>
												</button>
											</td>
										</tr>
									);
							})}

						</tbody>
					</table>
				</div>
			);
	}

}